import React from "react";
import styled from "styled-components";
import axios from "axios";
import { Link } from "react-router-dom";
import { render } from "@testing-library/react";
async function getDatas(url) {
  const proxyurl = "https://cors-anywhere.herokuapp.com/";
  const seperate = url.split("?")[0];
  const {
    data: { data }
  } = await axios.get(`${proxyurl}${seperate}?`, {
    params: {
      limit: 10
    },
    method: "GET",
    headers: {
      "x-rapidapi-host": "deezerdevs-deezer.p.rapidapi.com",
      "x-rapidapi-key": "03afeef36cmsh1bdcf2e9a60f303p19457cjsn1f84e9465bd0"
    }
  });
  return data;
}

class Artist extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      datas: ""
    };
  }
  // export default ({ id, artist }) => {
  componentDidMount() {
    let origin = getDatas(this.props.artist.tracklist);
    origin.then(res => {
      this.setState({
        datas: res
      });
      // console.log(this.state.datas)
    });
  }
  render() {
    const {
      props: { artist }
    } = this;
    return (
      <Contaier>
        <Poster poster={artist.picture_medium}></Poster>
        <Link
          to={`/artist/${artist.name}`}
          style={{ textDecoration: "none", color: "black" }}
        >
          {this.state.datas ? <Name datas={this.state.datas}>{artist.name}</Name> : null}
        </Link>
      </Contaier>
    );
  }
}
export default Artist;

const Contaier = styled.div`
  width: 100%;
  font-size: 1.1rem;
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  background-color: rgba(240,240,240,0.5);
  
`;
const Name = styled.div``;
const Poster = styled.div`
  background-image: url(${props => props.poster});
  width: 70px;
  height: 100px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
`;
